import React, { useRef, useState, useEffect } from 'react';
import '../index.css';

import { SQUARE_SIZE, BLOB_SIZE } from '../index.js';

function Blob(props) {
    const ref = useRef();

    const offset = (SQUARE_SIZE - BLOB_SIZE) / 2;
    const top = SQUARE_SIZE * props.row + offset;
    const left = SQUARE_SIZE * props.col + offset;

    // Set start location
    useEffect(() => {
        ref.current.style.top = top;
        ref.current.style.left = left;
    }, [])


    const updateTimer = useRef(null);

    // On change props, set initial position as same as owner blob's, after 100ms set it again.  
    function updateLocation() {
        if (props.selectedBlob) {
            const ownerTop = SQUARE_SIZE * props.selectedBlob.row + offset;
            const ownerLeft = SQUARE_SIZE * props.selectedBlob.col + offset;
            ref.current.style.top = ownerTop;
            ref.current.style.left = ownerLeft;
            updateTimer.current = setTimeout(() => {
                ref.current.style.top = top;
                ref.current.style.left = left;
                updateTimer.current = null;
            }, 1);
        }
    }

    // Call updateLocation on row or col change.
    useEffect(() => {
        if (!updateTimer.current) {
            updateLocation();
        }
    }, [props.col, props.row]);


    let className = 'blob ' + props.color;
    if (props.highlight) className += ' highlight';
    return (
        <div ref={ref} className={className}>
            {props.highlight &&
                <div className='face'>
                    <div className='eye'>
                        <div className='upper-pupil'></div>
                        <div className='iris'></div>
                        <div className='lower-pupil'></div>
                    </div>
                    <div className='eye'>
                        <div className='upper-pupil'></div>
                        <div className='iris'></div>
                        <div className='lower-pupil'></div>
                    </div>
                </div>
            }

        </div>
    );
}

export default Blob;
