import React from 'react';
import '../index.css';

import { ROW_SIZE, COL_SIZE } from '../index.js';
import Square from './Square.js';


class Board extends React.Component {
    renderSquare(i) {
        const clickedRow = Math.floor(i / ROW_SIZE);
        const clickedCol = i % COL_SIZE;
        let highlightBlob = false;
        if (this.props.selectedBlob && !this.props.firstClick) highlightBlob = this.props.selectedBlob.row === clickedRow && this.props.selectedBlob.col === clickedCol ? true : false;
        let highlightNeighbors = false;
        if (this.props.availableNeighbors) highlightNeighbors = this.props.availableNeighbors.find(el => el[0] === clickedRow && el[1] === clickedCol);
        let highlightJumps = false;
        if (this.props.availableJumps) highlightJumps = this.props.availableJumps.find(el => el[0] === clickedRow && el[1] === clickedCol);

        return (
            <Square
                value={this.props.squares[clickedRow][clickedCol]}
                onClick={() => this.props.onClick(i)}
                selectedBlob={this.props.selectedBlob}
                highlightBlob={highlightBlob}
                highlightNeighbors={highlightNeighbors}
                highlightJumps={highlightJumps}
                key={"square" + i}
                id = {"square" + i}
                row = {clickedRow}
                col = {clickedCol}
            />
        );
    }

    render() {
        let squareList = [];
        for (let i = 0; i < ROW_SIZE; i++) {
            let column = [];
            for (let j = 0; j < COL_SIZE; j++) {
                column.push(this.renderSquare(i * ROW_SIZE + j));
            }
            squareList.push(<div className="board-row" id = {"row" + i} key={"row" + i}>{column}</div>);
        }

        return (
            <div>
                {squareList}
            </div>
        );
    }
}

export default Board;