import React from 'react';
import '../index.css';

import Blob from './Blob.js'

class Square extends React.Component {
   
    render() {
        let className = "square ";
        if (this.props.highlightNeighbors) className += 'neighbor';
        else if (this.props.highlightJumps) className += 'jump';

        const blobColor = this.props.value == 'X' ? 'red' : 'blue';

        return(
            <button
                className={className}
                onClick={this.props.onClick}
            >
                {this.props.value && 
                <Blob 
                color={blobColor} 
                highlight={this.props.highlightBlob}
                row={this.props.row}
                col={this.props.col}
                selectedBlob={this.props.selectedBlob}
                />}
                
            </button>
        )
    };
}

export default Square;